@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

body{
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}

.App{
  height: 100%;
  display: flex;

}
.space{
  letter-spacing: 5px;
}

.boxleft{
  text-align:start  ;
  background-color: #dee2e6;
  width: 30%;
  padding-left: 10px;
}

.title{
  .smith{
    font-weight: lighter;
  }
  text-align: center;
  padding-bottom: 10px;
}

.image{
  border-radius: 50%;
  height: 150px;
  width: 150px;
}

.userdetail{
  text-align: start;
  font-family: 'Poppins', sans-serif;
  padding-bottom:10px;
  font-size:14px;
}
.mail,.ph,.loc{
  position: relative;
  bottom:1px;
  top: 2px;
}


.social{
  >h4 {
    font-size: 14px;
  }
  padding-bottom:10px;
}
.lin,.fac,.ins{
  position: relative;
  bottom:1px;
  top: 2px;
}

.profile{
  >p{
    text-align: start;
    font-size: 15px;
    padding-left: 4px;
    font-family: 'Poppins', sans-serif;
  }
  padding-bottom:10px;
}

.skill{
  padding-top: 20px;
  padding-bottom: 20px;
}

.ul{
  columns: 2;
  }


/* boxright */

.boxright{
  width:70%;
  padding-left: 20px;
}

.experince{
  padding-bottom: 20px
}

.education{
  padding-bottom:30px;
  padding-left: 20px;
}

.edcontent{
  display: flex;
}

.educationimage{
  padding-right:1rem;
}

.educationbullet{
  /* position: relative; */
  display: block;
  width: 16px;
  height: 16px;
  background-color: gray;
  border-radius: 50%;
  margin-top: 1.5rem;
}

.educationline{
    display: block;
    width: 2px;
    height: 100%;
    background-color: gray;
    transform: translate(7px, 0);
}
.eddetail{
  display: flex;
  flex-direction: column;
}
.edname{
  margin-bottom:0px;
}
.edyear{
  margin-bottom:0px;
  padding-bottom:10px;
}

.eddesc{
  font-size:14px;
}
.cert{
  padding-top: 10px;
}

.ref{
  padding-top: 10px;
  /* padding-bottom: 10px; */
}
.col{
  columns: 2;
  text-align:start;
}

.col2{
  padding-top:10px;
}

.lan{
  columns: 3;
  padding-top: 10px;
}

.lang{
  padding-top: 20px;
  padding-bottom:20px;
}

.inter{
  padding-top: 20px;
  list-style: none;
  font-size: 18px;
  columns: 4;
  text-align: center;
}

